import React from 'react';
import { User, Gift, Star, Sparkles } from 'lucide-react';

const BirthdayTemplate = ({ data }) => {
    const { title, subtitle, date, image, message, variant = 1, location, time, phone } = data;

    // Variant 1: Royal Gold (Previous) - Enhanced
    if (variant === 1) {
        return (
            <div className="relative w-full h-full bg-white flex overflow-hidden">
                <div className="w-[35%] h-full bg-neutral-900 relative flex flex-col justify-between p-6 z-10">
                    <div className="absolute top-0 left-0 w-full h-full overflow-hidden pointer-events-none">
                        <div className="absolute top-[-50px] left-[-50px] w-[150px] h-[150px] border-[20px] border-amber-500 rounded-full opacity-20"></div>
                        <div className="absolute bottom-[20%] right-[-40px] w-[100px] h-[100px] bg-amber-600/20 transform rotate-45"></div>
                    </div>

                    <div className="relative z-10 pt-10">
                        <div className="w-12 h-1 bg-amber-500 mb-6"></div>
                        <div className="space-y-2">
                            <div className="w-2 h-2 rounded-full bg-amber-500"></div>
                            <div className="w-2 h-2 rounded-full bg-neutral-700"></div>
                            <div className="w-2 h-2 rounded-full bg-neutral-700"></div>
                        </div>
                    </div>

                    <div className="relative z-10 mb-10">
                        <div className="bg-amber-600 text-black font-bold text-center py-2 px-4 uppercase tracking-[0.2em] text-[10px] shadow-lg mb-4 transform -skew-x-12 inline-block">
                            {date || "Celebrate"}
                        </div>
                        <h1 className="text-amber-400 font-bold text-5xl uppercase tracking-tighter leading-[0.9] break-words font-serif max-w-full drop-shadow-sm">
                            {subtitle || "Name"}
                        </h1>
                    </div>
                </div>

                <div className="w-[65%] h-full relative bg-white flex flex-col">
                    <div className="flex-1 p-8 flex flex-col items-start justify-center relative bg-white">
                        <h2 className="font-serif italic text-6xl text-black z-10 relative">
                            {title || "Happy"} <br />
                            <span className="text-7xl font-black uppercase tracking-tighter text-neutral-900 block mt-[-5px] not-italic leading-none">Birthday</span>
                        </h2>
                        <p className="mt-4 text-neutral-500 text-xs max-w-xs leading-relaxed border-l-4 border-amber-500 pl-4 italic">
                            {message || "Wishing you yet the most amazing birthday ever. Live long and prosper in your new age."}
                        </p>
                        <div className="absolute top-10 right-10 w-20 h-20 bg-black rounded-full flex items-center justify-center border-4 border-amber-500 shadow-xl transform rotate-12">
                            <span className="text-amber-400 font-bold text-2xl">+1</span>
                        </div>
                    </div>

                    <div className="h-[55%] w-full relative p-4 pl-0">
                        <div className="w-full h-full relative overflow-hidden rounded-l-[50px] shadow-2xl border-4 border-white">
                            {image ? (
                                <img src={image} alt="Birthday Person" className="w-full h-full object-cover" />
                            ) : (
                                <div className="w-full h-full bg-neutral-200 flex items-center justify-center text-neutral-400">
                                    <span className="text-sm uppercase tracking-widest">No Image Uploaded</span>
                                </div>
                            )}
                            <div className="absolute inset-0 bg-gradient-to-t from-black/60 to-transparent"></div>
                        </div>
                        <div className="absolute top-[-20px] left-10 w-16 h-16 bg-amber-500 rounded-full border-4 border-white z-20 shadow-lg"></div>
                    </div>
                </div>
            </div>
        );
    }

    // Variant 2: Floral Dream (Soft, Beautiful)
    if (variant === 2) {
        return (
            <div className="relative w-full h-full bg-[#fffcf9] flex flex-col p-8 overflow-hidden font-sans">
                {/* Decorative Flowers (SVG or Gradient circles) */}
                <div className="absolute top-[-50px] right-[-50px] w-64 h-64 bg-pink-100 rounded-full opacity-50 blur-3xl"></div>
                <div className="absolute bottom-[-50px] left-[-50px] w-64 h-64 bg-amber-100 rounded-full opacity-50 blur-3xl"></div>

                <div className="relative z-10 flex-1 flex flex-col items-center justify-center text-center">
                    <div className="mb-4">
                        <span className="text-pink-400 uppercase tracking-[0.4em] text-[10px] font-bold">In Honor Of</span>
                        <div className="h-[1px] w-12 bg-pink-200 mx-auto mt-2"></div>
                    </div>

                    <h1 className="font-serif text-6xl text-neutral-800 mb-2 leading-tight px-4 break-words">
                        {subtitle || "Sarah Jane"}
                    </h1>
                    <h2 className="font-serif italic text-2xl text-pink-500 mb-6 drop-shadow-sm">
                        {title || "Happy Birthday"}
                    </h2>

                    <div className="w-48 h-48 relative mb-6">
                        <div className="absolute inset-0 border-4 border-white shadow-xl rounded-full overflow-hidden rotate-3">
                            {image ? (
                                <img src={image} alt="Birthday Person" className="w-full h-full object-cover" />
                            ) : (
                                <div className="w-full h-full bg-pink-50 flex items-center justify-center">
                                    <User className="w-12 h-12 text-pink-100" />
                                </div>
                            )}
                        </div>
                        {/* Floral Decoration */}
                        <div className="absolute -top-4 -right-4 w-12 h-12 bg-amber-400 rounded-full flex items-center justify-center text-white shadow-lg animate-bounce duration-[3000ms]">âœ¨</div>
                    </div>

                    <p className="max-w-xs text-neutral-600 text-xs leading-relaxed mb-6 italic px-4">
                        "{message || "A beautiful soul like yours deserves a beautiful day like today. Have the best one yet!"}"
                    </p>

                    <div className="flex items-center gap-4 text-neutral-300">
                        <div className="h-[1px] w-12 bg-neutral-200"></div>
                        <span className="text-[9px] uppercase tracking-widest font-bold text-neutral-500">{date || "March 16, 2024"}</span>
                        <div className="h-[1px] w-12 bg-neutral-200"></div>
                    </div>
                </div>

                <div className="absolute bottom-8 right-8">
                    <div className="text-pink-300 font-serif text-6xl opacity-20 select-none">Rose</div>
                </div>
            </div>
        );
    }

    // Variant 3: Minimal Dark (Modern & Sleek)
    if (variant === 3) {
        return (
            <div className="relative w-full h-full bg-neutral-950 text-white flex flex-col p-0 overflow-hidden font-sans">
                {/* Background Image with Overlay */}
                <div className="absolute inset-0 z-0">
                    {image ? (
                        <img src={image} alt="Background" className="w-full h-full object-cover opacity-50 grayscale" />
                    ) : (
                        <div className="w-full h-full bg-neutral-900 opacity-40"></div>
                    )}
                    <div className="absolute inset-0 bg-gradient-to-t from-neutral-950 via-neutral-950/70 to-transparent"></div>
                </div>

                <div className="relative z-10 flex-1 flex flex-col justify-end p-12">
                    <div className="mb-10">
                        <div className="w-16 h-[2.5px] bg-white mb-6"></div>
                        <h2 className="text-white/60 uppercase tracking-[0.5em] text-[10px] font-bold mb-4 drop-shadow-md">{title || "Happy Birthday"}</h2>
                        <h1 className="text-7xl font-black uppercase tracking-tighter leading-[0.85] mb-6 max-w-sm break-words drop-shadow-2xl">
                            {subtitle || "BRIAN"}
                        </h1>
                        <p className="max-w-xs text-neutral-300 text-xs font-medium leading-relaxed border-l-2 border-white/20 pl-6 drop-shadow-md">
                            {message || "Pushing boundaries. Embracing growth. Another year of excellence starts now."}
                        </p>
                    </div>

                    <div className="flex justify-between items-end">
                        <div className="space-y-1">
                            <span className="block text-[8px] uppercase tracking-[0.3em] text-neutral-500 font-bold">Commencement</span>
                            <span className="block text-xs font-bold tracking-widest opacity-80">{date || "OCTOBER 12, 2024"}</span>
                        </div>
                        <div className="w-14 h-14 border-2 border-white/30 rounded-full flex items-center justify-center font-bold text-[10px] backdrop-blur-sm">
                            {new Date().getFullYear()}
                        </div>
                    </div>
                </div>

                {/* Vertical Stripe */}
                <div className="absolute top-0 right-12 w-[1px] h-full bg-white/5"></div>
            </div>
        );
    }

    // Variant 4: Festive Balloons (Balloons, Gifts, Black & Gold)
    if (variant === 4) {
        return (
            <div className="relative w-full h-full bg-black text-white p-0 overflow-hidden font-sans">
                {/* Background Confetti/Stars */}
                <div className="absolute inset-0 opacity-40 pointer-events-none">
                    {Array.from({ length: 50 }).map((_, i) => (
                        <div
                            key={i}
                            className="absolute bg-amber-400 rounded-full"
                            style={{
                                width: Math.random() * 3 + 'px',
                                height: Math.random() * 3 + 'px',
                                top: Math.random() * 100 + '%',
                                left: Math.random() * 100 + '%',
                                opacity: Math.random(),
                                transform: `rotate(${Math.random() * 360}deg)`
                            }}
                        />
                    ))}
                    <div className="absolute top-10 left-10 text-amber-500/20"><Star className="w-20 h-20 rotate-12" /></div>
                    <div className="absolute top-[40%] right-20 text-amber-500/20"><Star className="w-12 h-12 -rotate-12" /></div>
                </div>

                <div className="relative z-10 h-full flex flex-col p-10">
                    <div className="mt-12">
                        <h2 className="text-amber-500 font-bold tracking-[0.4em] uppercase text-sm mb-2 drop-shadow-md">
                            {title || "HAPPY"}
                        </h2>
                        <h1 className="text-7xl font-serif italic text-white leading-none mb-8 drop-shadow-xl">
                            {subtitle?.split(' ')[0] || "Birthday"}
                        </h1>
                        <p className="max-w-[240px] text-gray-300 text-xs leading-relaxed font-medium">
                            {message || "Wishing you a day filled with love, laughter, and all the things that make birthdays extraordinary."}
                        </p>
                    </div>

                    <div className="flex-1 flex items-end justify-between pb-10">
                        {/* Gifts at bottom */}
                        <div className="flex gap-2">
                            <div className="w-16 h-12 bg-neutral-800 border-t-4 border-amber-600 rounded-sm shadow-lg relative flex items-center justify-center">
                                <div className="absolute -top-3 w-4 h-4 bg-amber-500 rotate-45"></div>
                                <Gift className="w-6 h-6 text-amber-500/50" />
                            </div>
                            <div className="w-12 h-16 bg-neutral-900 border-t-4 border-amber-500 rounded-sm shadow-lg relative flex items-center justify-center">
                                <div className="absolute -top-3 w-4 h-4 bg-amber-400 rotate-45"></div>
                                <Gift className="w-4 h-4 text-amber-400/50" />
                            </div>
                        </div>

                        {/* Image Frame */}
                        <div className="w-32 h-44 bg-neutral-900 border-2 border-amber-500 rounded-xl overflow-hidden shadow-2xl rotate-3 relative">
                            {image ? (
                                <img src={image} alt="Celebrant" className="w-full h-full object-cover" />
                            ) : (
                                <div className="w-full h-full flex flex-col items-center justify-center text-amber-900/40">
                                    <User className="w-10 h-10" />
                                    <span className="text-[8px] font-bold uppercase mt-2 italic">Your Photo</span>
                                </div>
                            )}
                            <div className="absolute inset-0 bg-gradient-to-t from-black/40 to-transparent"></div>
                        </div>
                    </div>
                </div>

                {/* Balloons on the Right */}
                <div className="absolute top-[20%] right-[-40px] flex flex-col gap-[-20px] pointer-events-none">
                    {/* Gold Balloon */}
                    <div className="w-24 h-32 bg-gradient-to-br from-amber-300 via-amber-500 to-amber-700 rounded-full shadow-[0_10px_30px_rgba(251,191,36,0.5)] animate-bounce duration-[4000ms] relative">
                        <div className="absolute top-4 left-4 w-4 h-8 bg-white/20 rounded-full blur-sm"></div>
                    </div>
                    {/* Black Balloon */}
                    <div className="w-24 h-32 bg-gradient-to-br from-neutral-700 via-neutral-900 to-black rounded-full shadow-[0_10px_30px_rgba(0,0,0,0.8)] mt-[-40px] ml-10 animate-bounce duration-[5000ms] relative">
                        <div className="absolute top-4 left-4 w-4 h-8 bg-white/10 rounded-full blur-sm"></div>
                    </div>
                    {/* Gold Balloon */}
                    <div className="w-20 h-28 bg-gradient-to-br from-amber-400 via-amber-600 to-amber-800 rounded-full shadow-[0_10px_30px_rgba(251,191,36,0.3)] mt-[-30px] ml-[-10px] animate-bounce duration-[4500ms] relative">
                        <div className="absolute top-3 left-3 w-3 h-6 bg-white/20 rounded-full blur-sm"></div>
                    </div>
                </div>
            </div>
        );
    }

    // Variant 5: Golden Party Invitation (EXACT match to first image)
    if (variant === 5) {
        return (
            <div className="relative w-full h-full bg-black text-white overflow-hidden font-sans">
                {/* Dotted background pattern */}
                <div className="absolute inset-0">
                    {Array.from({ length: 300 }).map((_, i) => (
                        <div
                            key={i}
                            className="absolute bg-amber-400 rounded-full opacity-60"
                            style={{
                                width: '2px',
                                height: '2px',
                                top: Math.random() * 100 + '%',
                                left: Math.random() * 100 + '%',
                            }}
                        />
                    ))}
                </div>

                {/* Top balloons - left side */}
                <div className="absolute top-8 left-8 flex gap-3">
                    <div className="w-16 h-22 bg-gradient-to-b from-amber-300 to-amber-600 rounded-full shadow-xl relative">
                        <div className="absolute top-3 left-3 w-4 h-8 bg-white/30 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-12 bg-amber-700"></div>
                    </div>
                    <div className="w-14 h-20 bg-gradient-to-b from-neutral-700 to-black rounded-full shadow-xl relative">
                        <div className="absolute top-3 left-3 w-3 h-6 bg-white/20 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-10 bg-neutral-800"></div>
                    </div>
                    <div className="w-15 h-21 bg-gradient-to-b from-amber-400 to-amber-700 rounded-full shadow-xl relative">
                        <div className="absolute top-3 left-3 w-3 h-7 bg-white/30 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-11 bg-amber-800"></div>
                    </div>
                </div>

                {/* Top balloons - right side */}
                <div className="absolute top-8 right-8 flex gap-3">
                    <div className="w-15 h-21 bg-gradient-to-b from-amber-300 to-amber-600 rounded-full shadow-xl relative">
                        <div className="absolute top-3 left-3 w-3 h-7 bg-white/30 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-11 bg-amber-700"></div>
                    </div>
                    <div className="w-14 h-20 bg-gradient-to-b from-neutral-700 to-black rounded-full shadow-xl relative">
                        <div className="absolute top-3 left-3 w-3 h-6 bg-white/20 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-10 bg-neutral-800"></div>
                    </div>
                </div>

                {/* Center content */}
                <div className="flex flex-col items-center justify-center h-full px-8 relative z-10">
                    {/* Diamond frame with invitation text */}
                    <div className="relative mb-8">
                        {/* Background diamond shapes */}
                        <div className="absolute inset-0 flex items-center justify-center">
                            <div className="w-80 h-20 border-2 border-amber-500/30 transform rotate-45"></div>
                            <div className="absolute w-72 h-16 border border-amber-400/50 transform rotate-45"></div>
                        </div>
                        
                        {/* Invitation text box */}
                        <div className="relative bg-black border border-amber-500 px-8 py-4 transform -skew-x-12">
                            <span className="transform skew-x-12 text-sm font-bold tracking-[0.3em] text-amber-400 uppercase">
                                YOU ARE INVITED TO MY
                            </span>
                        </div>
                    </div>

                    {/* Main Birthday text */}
                    <h1 className="text-8xl font-serif italic text-amber-400 mb-4 drop-shadow-2xl leading-none">
                        {subtitle || "Birthday"}
                    </h1>
                    <h2 className="text-4xl font-bold tracking-[0.8em] text-amber-500 uppercase mb-12">
                        PARTY
                    </h2>

                    {/* Date section */}
                    <div className="text-center mb-8">
                        <p className="text-2xl font-bold tracking-[0.3em] text-white mb-2">
                            MARCH
                        </p>
                        <div className="flex items-center justify-center gap-4 text-white mb-2">
                            <span className="text-lg font-medium">SUNDAY</span>
                            <div className="w-2 h-2 bg-amber-500 rounded-full"></div>
                            <span className="text-5xl font-bold">16</span>
                            <div className="w-2 h-2 bg-amber-500 rounded-full"></div>
                            <span className="text-lg font-medium">AT 5 PM</span>
                        </div>
                        <p className="text-2xl font-bold tracking-[0.2em] text-white">
                            2024
                        </p>
                    </div>

                    {/* Contact info */}
                    <div className="text-center">
                        <p className="text-base font-bold tracking-widest text-amber-400 mb-1">
                            {phone || "+123-456-7890"}
                        </p>
                        <p className="text-sm font-medium tracking-wider text-gray-300 uppercase">
                            {location || "123 ANYWHERE ST., ANY CITY"}
                        </p>
                    </div>
                </div>

                {/* Bottom right balloons */}
                <div className="absolute bottom-8 right-8 flex gap-2">
                    <div className="w-16 h-22 bg-gradient-to-b from-amber-300 to-amber-600 rounded-full shadow-xl relative">
                        <div className="absolute top-3 left-3 w-4 h-8 bg-white/30 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-12 bg-amber-700"></div>
                    </div>
                    <div className="w-14 h-20 bg-gradient-to-b from-neutral-700 to-black rounded-full shadow-xl relative">
                        <div className="absolute top-3 left-3 w-3 h-6 bg-white/20 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-10 bg-neutral-800"></div>
                    </div>
                    <div className="w-12 h-18 bg-gradient-to-b from-amber-400 to-amber-700 rounded-full shadow-xl relative">
                        <div className="absolute top-2 left-2 w-3 h-6 bg-white/30 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-9 bg-amber-800"></div>
                    </div>
                </div>
            </div>
        );
    }

    // Variant 6: Modern Photo Birthday Card (EXACT match to second image)
    if (variant === 6) {
        return (
            <div className="relative w-full h-full bg-white flex overflow-hidden font-sans">
                {/* Left Side - Dark with geometric pattern */}
                <div className="w-1/2 h-full bg-black relative overflow-hidden">
                    {/* Geometric pattern background */}
                    <div className="absolute inset-0 opacity-30">
                        {/* Various geometric shapes */}
                        {Array.from({ length: 40 }).map((_, i) => {
                            const shapes = ['circle', 'square', 'line'];
                            const shape = shapes[i % 3];
                            const size = Math.random() * 15 + 5;
                            
                            if (shape === 'circle') {
                                return (
                                    <div
                                        key={i}
                                        className="absolute bg-gray-600 rounded-full"
                                        style={{
                                            width: size + 'px',
                                            height: size + 'px',
                                            top: Math.random() * 100 + '%',
                                            left: Math.random() * 100 + '%',
                                        }}
                                    />
                                );
                            } else if (shape === 'square') {
                                return (
                                    <div
                                        key={i}
                                        className="absolute bg-orange-500"
                                        style={{
                                            width: size + 'px',
                                            height: size + 'px',
                                            top: Math.random() * 100 + '%',
                                            left: Math.random() * 100 + '%',
                                            transform: `rotate(${Math.random() * 360}deg)`
                                        }}
                                    />
                                );
                            } else {
                                return (
                                    <div
                                        key={i}
                                        className="absolute bg-gray-500"
                                        style={{
                                            width: size * 2 + 'px',
                                            height: '2px',
                                            top: Math.random() * 100 + '%',
                                            left: Math.random() * 100 + '%',
                                            transform: `rotate(${Math.random() * 360}deg)`
                                        }}
                                    />
                                );
                            }
                        })}
                    </div>

                    {/* Orange pin at top right */}
                    <div className="absolute top-8 right-8 w-8 h-8 bg-orange-500 rounded-full shadow-lg transform rotate-45 relative">
                        <div className="absolute top-1 left-1 w-3 h-3 bg-white/50 rounded-full"></div>
                    </div>

                    {/* Photo frame - positioned to match the image */}
                    <div className="absolute bottom-0 left-0 w-full h-[80%] p-4">
                        <div className="w-full h-full relative">
                            {/* Orange frame border */}
                            <div className="absolute inset-0 bg-orange-500 p-1">
                                <div className="w-full h-full bg-gray-200 relative overflow-hidden">
                                    {image ? (
                                        <img src={image} alt="Birthday Person" className="w-full h-full object-cover grayscale" />
                                    ) : (
                                        <div className="w-full h-full bg-gray-300 flex items-center justify-center">
                                            <div className="text-center">
                                                <User className="w-20 h-20 text-gray-400 mx-auto mb-4" />
                                                <span className="text-lg text-gray-500 font-medium">Photo Here</span>
                                            </div>
                                        </div>
                                    )}
                                    <div className="absolute inset-0 bg-gradient-to-t from-black/20 to-transparent"></div>
                                </div>
                            </div>
                            
                            {/* Name tag at bottom */}
                            <div className="absolute bottom-4 left-1/2 transform -translate-x-1/2 bg-orange-500 text-white px-8 py-3 font-bold text-xl tracking-wider uppercase">
                                {subtitle || "AMOS"}
                            </div>
                        </div>
                    </div>
                </div>

                {/* Right Side - White background with text */}
                <div className="w-1/2 h-full bg-white flex flex-col justify-center items-start p-12 relative">
                    {/* Happy Birthday Text */}
                    <div className="mb-8">
                        <h1 className="text-5xl font-serif italic text-orange-500 mb-3 leading-tight">
                            {title || "Happy"}
                        </h1>
                        <h2 className="text-6xl font-black uppercase text-black tracking-tight leading-none">
                            BIRTHDAY
                        </h2>
                    </div>

                    {/* Message */}
                    <p className="text-gray-600 text-base leading-relaxed mb-8 max-w-sm">
                        {message || "Wishing you yet the most amazing birthday ever. Live long and prosper in your new age"}
                    </p>

                    {/* Age badge with decorative elements */}
                    <div className="relative">
                        <div className="w-20 h-20 bg-black rounded-full flex items-center justify-center text-white font-bold text-2xl relative z-10">
                            <span>+1</span>
                        </div>
                        {/* Orange circle decoration */}
                        <div className="absolute -top-3 -right-3 w-10 h-10 bg-orange-500 rounded-full"></div>
                        {/* Black circle with orange border */}
                        <div className="absolute -bottom-3 -left-3 w-8 h-8 bg-black rounded-full border-2 border-orange-500"></div>
                    </div>

                    {/* Decorative elements */}
                    <div className="absolute top-12 right-12 w-6 h-6 bg-orange-500 transform rotate-45"></div>
                    <div className="absolute bottom-12 right-12 w-4 h-4 bg-gray-400 rounded-full"></div>
                </div>
            </div>
        );
    }

    // Variant 7: Elegant Balloons & Gifts (EXACT match to third image)
    if (variant === 7) {
        return (
            <div className="relative w-full h-full bg-black text-white flex flex-col justify-center items-center p-8 overflow-hidden font-sans">
                {/* Golden confetti/streamers background - exactly like the image */}
                <div className="absolute inset-0 overflow-hidden">
                    {/* Curved streamers */}
                    {Array.from({ length: 40 }).map((_, i) => (
                        <div
                            key={`streamer-${i}`}
                            className="absolute bg-amber-400 opacity-80"
                            style={{
                                width: Math.random() * 25 + 15 + 'px',
                                height: '3px',
                                top: Math.random() * 100 + '%',
                                left: Math.random() * 100 + '%',
                                transform: `rotate(${Math.random() * 360}deg)`,
                                borderRadius: '2px'
                            }}
                        />
                    ))}
                    {/* Small confetti dots */}
                    {Array.from({ length: 60 }).map((_, i) => (
                        <div
                            key={`confetti-${i}`}
                            className="absolute bg-amber-300 opacity-90"
                            style={{
                                width: '4px',
                                height: '4px',
                                top: Math.random() * 100 + '%',
                                left: Math.random() * 100 + '%',
                                borderRadius: '50%'
                            }}
                        />
                    ))}
                </div>

                {/* Main content - centered */}
                <div className="relative z-10 text-center mb-16">
                    <h1 className="text-6xl font-bold text-amber-400 mb-6 tracking-wide drop-shadow-lg">
                        {title || "HAPPY"}
                    </h1>
                    <h2 className="text-8xl font-serif italic text-white mb-8 leading-none drop-shadow-2xl">
                        {subtitle || "Birthday"}
                    </h2>
                    <p className="text-gray-300 text-base leading-relaxed max-w-md mx-auto">
                        {message || "Wishing you a day filled with love, laughter, and all the things that make birthdays extraordinary."}
                    </p>
                </div>

                {/* Balloons cluster - Right side, exactly positioned like the image */}
                <div className="absolute right-16 top-1/4 flex flex-col gap-1">
                    {/* Top gold balloon */}
                    <div className="w-20 h-28 bg-gradient-to-b from-amber-300 to-amber-600 rounded-full shadow-2xl relative transform -rotate-12">
                        <div className="absolute top-4 left-4 w-5 h-10 bg-white/30 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-16 bg-amber-700"></div>
                    </div>
                    
                    {/* Black balloon - slightly overlapping */}
                    <div className="w-18 h-26 bg-gradient-to-b from-neutral-700 to-black rounded-full shadow-2xl relative transform rotate-8 ml-6 -mt-6">
                        <div className="absolute top-4 left-4 w-4 h-8 bg-white/20 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-14 bg-neutral-800"></div>
                    </div>
                    
                    {/* Medium gold balloon */}
                    <div className="w-17 h-25 bg-gradient-to-b from-amber-400 to-amber-700 rounded-full shadow-2xl relative transform -rotate-6 -mt-4">
                        <div className="absolute top-3 left-3 w-4 h-8 bg-white/30 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-13 bg-amber-800"></div>
                    </div>
                    
                    {/* Small black balloon */}
                    <div className="w-15 h-22 bg-gradient-to-b from-neutral-600 to-black rounded-full shadow-2xl relative transform rotate-12 ml-8 -mt-5">
                        <div className="absolute top-3 left-3 w-3 h-7 bg-white/20 rounded-full blur-sm"></div>
                        <div className="absolute bottom-0 left-1/2 transform -translate-x-1/2 translate-y-full w-0.5 h-11 bg-neutral-800"></div>
                    </div>
                </div>

                {/* Gift boxes - Bottom left, exactly like the image */}
                <div className="absolute bottom-16 left-16 flex gap-4">
                    {/* Large gold gift box */}
                    <div className="w-24 h-20 bg-gradient-to-b from-amber-600 to-amber-800 relative shadow-2xl">
                        {/* Horizontal ribbon */}
                        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 -translate-y-1 w-20 h-4 bg-amber-400"></div>
                        {/* Vertical ribbon */}
                        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 -translate-y-2 w-4 h-8 bg-amber-300"></div>
                        {/* Bow on top */}
                        <div className="absolute -top-3 left-1/2 transform -translate-x-1/2">
                            <div className="w-6 h-3 bg-amber-200 rounded-full"></div>
                            <div className="absolute top-0 left-1/2 transform -translate-x-1/2 w-2 h-2 bg-amber-100 rounded-full"></div>
                        </div>
                        <div className="absolute inset-0 bg-gradient-to-br from-white/15 to-transparent"></div>
                    </div>
                    
                    {/* Tall black gift box */}
                    <div className="w-18 h-24 bg-gradient-to-b from-neutral-800 to-black relative shadow-2xl">
                        {/* Horizontal ribbon */}
                        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 -translate-y-1 w-14 h-3 bg-amber-500"></div>
                        {/* Vertical ribbon */}
                        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 -translate-y-2 w-3 h-7 bg-amber-400"></div>
                        {/* Bow on top */}
                        <div className="absolute -top-2 left-1/2 transform -translate-x-1/2">
                            <div className="w-5 h-2 bg-amber-300 rounded-full"></div>
                        </div>
                        <div className="absolute inset-0 bg-gradient-to-br from-white/10 to-transparent"></div>
                    </div>
                    
                    {/* Small gold gift box */}
                    <div className="w-16 h-14 bg-gradient-to-b from-amber-500 to-amber-700 relative shadow-2xl">
                        {/* Horizontal ribbon */}
                        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 -translate-y-1 w-12 h-2 bg-amber-300"></div>
                        {/* Vertical ribbon */}
                        <div className="absolute top-0 left-1/2 transform -translate-x-1/2 -translate-y-1 w-2 h-5 bg-amber-200"></div>
                        {/* Small bow */}
                        <div className="absolute -top-1 left-1/2 transform -translate-x-1/2 w-3 h-1 bg-amber-100 rounded-full"></div>
                        <div className="absolute inset-0 bg-gradient-to-br from-white/15 to-transparent"></div>
                    </div>
                </div>

                {/* Sparkle effects - positioned like in the image */}
                <div className="absolute top-12 left-12 text-amber-400 opacity-70">
                    <Star className="w-8 h-8 animate-pulse" />
                </div>
                <div className="absolute top-20 right-32 text-amber-300 opacity-50">
                    <Star className="w-6 h-6 animate-pulse" style={{ animationDelay: '1s' }} />
                </div>
                <div className="absolute bottom-24 left-1/3 text-amber-500 opacity-60">
                    <Star className="w-5 h-5 animate-pulse" style={{ animationDelay: '2s' }} />
                </div>
            </div>
        );
    }

    return null;
};

export default BirthdayTemplate;
